body{
    background-color: beige;
    padding-bottom: 30px;
}

.main-container{
    margin-top: 30px;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;

}

.card p {
    padding: 5px;
    text-align: center;
    
}

.card img{
    max-height: 120px;
    width: auto;
}
